"use c"

import { Briefcase, Building2, Rocket } from "lucide-react";
import React from "react";

const RegisterPage = () => {
  const [businessType, setBusinessType] = React.useState<string | null>(
    "Freelancer"
  );

  const businessOptions = [
    { label: "Freelancer", icon: <Briefcase className="w-5 h-5" /> },
    { label: "SME", icon: <Building2 className="w-5 h-5" /> },
    { label: "Startup", icon: <Rocket className="w-5 h-5" /> },
  ];

  return (
    <div className="min-h-screen flex items-center justify-center w-full">
      <div className="w-full overflow-hidden flex items-stretch bg-white min-h-[500px] max-w-4xl shadow-sm rounded-xl">
        <div className="flex-1 px-4 gap-3 text-white bg-[#1b2a49] flex flex-col items-center justify-center">
          <h2 className="text-3xl font-semibold">Welcome</h2>
          <h4 className="text-center font-semibold text-sm leading-tight">
            Create your account and grow your business with us.
          </h4>
        </div>
        <div className="flex-3 px-6 py-6 flex flex-col">
          <h1 className="text-2xl mb-6 font-bold">Sign Up</h1>
          <form className="space-y-5">
            {/* Personal Information */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <input
                type="text"
                placeholder="Full Name"
                className="w-full border border-[#E1E8F5] text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4] rounded-lg px-4 py-2"
              />
              <input
                type="text"
                placeholder="Email"
                className="w-full border border-[#E1E8F5] text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4] rounded-lg px-4 py-2"
              />
              <input
                type="tel"
                placeholder="Phone Number"
                className="w-full border border-[#E1E8F5] text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4] rounded-lg px-4 py-2"
              />
              <input
                type="password"
                placeholder="Password"
                className="w-full border border-[#E1E8F5] text-[#344767] focus:outline-none focus:ring-2 focus:ring-[#2E69A4] rounded-lg px-4 py-2"
              />
            </div>
            {/* Business Type */}
            <div>
              <label className="block text-lg text-[#1B2A49] font-semibold mb-4">
                Choose Business Type
              </label>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                {businessOptions.map((item, index) => (
                  <div
                    key={index}
                    className={`border rounded-lg flex flex-col items-center justify-center p-4 cursor-pointer shadow-sm hover:shadow-md ${
                      item.label === businessType
                        ? "border-border-[#1B2A49] bg-[#F4F7FA]"
                        : "border-[#E1E8F5] bg-white"
                    }`}
                  >
                    {item.label}
                  </div>
                ))}
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default RegisterPage;
